import {Component, OnInit} from '@angular/core';
import {FormArray, FormBuilder, FormGroup, Validators} from '@angular/forms';
import {addProductParagraphs, sendjobRequest, sendsmallpara} from '../../home/home/homemutation';
import {Apollo} from 'apollo-angular';
import {getParagraph} from '../../auth/authMutation';
import {ActivatedRoute, Router} from '@angular/router';

@Component({
  selector: 'app-add-product',
  templateUrl: './add-paragraph.component.html',
  styleUrls: ['./add-paragraph.component.scss']
})
export class AddParagraphComponent implements OnInit {
  productData: FormGroup;
  smallPara: FormGroup;
  paragraphs: any;
  paragraphsToAddToProd: number[] = [];
  id: number;

  constructor(private fb: FormBuilder, private apollo: Apollo, private route: ActivatedRoute, private router: Router) {
  }

  ngOnInit(): void {
    this.route.params.subscribe(params => {
      this.id = params.id;
    });
    this.createParagraphForm();
    this.getAllParagraphs();
  }

  getAllParagraphs(): void {
    this.apollo.watchQuery({
      query: getParagraph, fetchPolicy: 'network-only'
    }).valueChanges.subscribe((result: any) => {
      this.paragraphs = result.data.findManySmallParagraphs.entities;
    });
  }


  addParagraphsIdsToProduct(ids): void {
    this.apollo.mutate({
      mutation: addProductParagraphs,
      variables: {id: Number(this.id), paragraphs: ids}
    }).subscribe((response) => {
      this.createParagraphForm();
      this.getAllParagraphs();
      alert('Done');
      this.router.navigateByUrl('/products/viewProduct/' + this.id);

    }, error => alert(error));
  }

  createParagraphForm(): void {
    this.smallPara = this.fb.group({
      headline: this.localizedObj(),
      fullDesc: this.fb.array([])
    });
  }

  localizedObj(): FormGroup {
    return this.fb.group({
      ar: ['', Validators.required],
      en: ['', Validators.required]
    });
  }

  get desc(): FormArray {
    return this.smallPara.get('fullDesc') as FormArray;
  }

  addDesc(): void {
    this.desc.push(this.localizedObj());
  }

  send(): void {
    this.apollo.mutate({
      mutation: sendsmallpara,
      variables: this.smallPara.value
    }).subscribe((response) => {
      this.createParagraphForm();
      this.getAllParagraphs();
      alert('Done');

    }, error => alert(error));
  }

  submit(): void {
    this.send();
  }

  removeDesc(i: number): void {
    this.desc.removeAt(i);
  }

}
