import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Apollo} from 'apollo-angular';
import {ActivatedRoute, Router} from '@angular/router';
import {oneProdQuery} from '../../home/home/homequeries';
import {createProduct, updateProduct, updateProductIcon, updateProductLogo} from '../../home/home/homemutation';

@Component({
  selector: 'app-add-edit-product',
  templateUrl: './add-edit-product.component.html',
  styleUrls: ['./add-edit-product.component.scss']
})
export class AddEditProductComponent implements OnInit {
  productForm: FormGroup;
  product: any;
  loading = true;
  id: any;
  icon: any;
  logo: any;
  toSend: any;
  toUpdate: any;
  lang = 'en';
  iconUrl: any;
  logoUrl: any;
  changeIcon: boolean = false;
  changeLogo: boolean = false;

  constructor(private fb: FormBuilder, private apollo: Apollo, private route: ActivatedRoute, private router: Router) {
    this.lang = localStorage.getItem('lang') || 'en';
  }

  isEditing(): boolean {
    return (this.id && (this.id !== null) && (this.id !== 'null') && (this.id !== undefined));
  }

  send(): void {

    this.apollo.mutate({
      mutation: createProduct,
      variables: this.toSend,
      context: {
        useMultipart: true
      }
    }).subscribe(response => {
      if (response.data) {
        alert('product created successfully');
        this.router.navigateByUrl('/products/list');

      }
    }, error => alert(error));
  }

  updateIcon(): void {
    this.apollo.mutate({
      mutation: updateProductIcon,
      variables: {id: this.toUpdate.id, icon: this.icon},
      context: {useMultipart: true}
    }).subscribe(response => {
    }, error => alert('Icon Not Updated'));
  }

  updateLogo(): void {
    this.apollo.mutate({
      mutation: updateProductLogo,
      variables: {id: this.toUpdate.id, logo: this.logo},
      context: {useMultipart: true}
    }).subscribe(response => {
    }, error => alert('logo not updated'));
  }

  update(): void {

  }

  submit(): void {
    this.toSend = this.productForm.value;
    this.toSend.icon = this.icon;
    this.toSend.logo = this.logo;
    this.isEditing() ? this.update() : this.send();
  }

  readURL(event: any, localVar): void {
    if (event.target.files && event.target.files[0]) {
      const file = event.target.files[0];
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = e => this[localVar] = reader.result;
    }
  }

  handleFileInput(type, e): void {
    if (type === 'icon') {
      if (e.target.files[0]) {
        this.icon = e.target.files[0];
        this.readURL(e, 'iconUrl');
      } else {
        this.icon = null;
        this.iconUrl = null;

      }

    } else if (type === 'logo') {
      if (e.target.files[0]) {
        this.logo = e.target.files[0];
        this.readURL(e, 'logoUrl');
        this.logoUrl = URL.createObjectURL(this.logo);
      } else {
        this.logo = null;
        this.logoUrl = null;
      }
    }
  }

  ngOnInit(): void {
    this.createNullForm();
    this.route.params.subscribe(params => {
      this.id = params.id;
      if (this.id && (this.id !== null) && (this.id !== 'null') && (this.id !== undefined)) {
        this.get();
      } else {
        this.createNullForm();
      }
    });
  }

  get(): void {
    this.apollo.watchQuery({
      query: oneProdQuery,
      fetchPolicy: 'network-only',
      variables: {id: Number(this.id)}
    }).valueChanges.subscribe((result: any) => {
      this.loading = false;
      this.product = result?.data?.findOneProduct;
      this.createExistingForm();
    });
  }

  createExistingForm(): void {
    this.productForm = this.fb.group({
      id: Number(this.id),
      onHome: [this.product.onHome],
      isActive: [this.product.isActive],
      title: this.fb.group({
        [this.lang]: [this.product.title, Validators.required],
        // [(this.lang === 'ar') ? 'en' : 'ar']: ['']
      }),
      subTitle: this.fb.group({
        [this.lang]: [this.product.subTitle, Validators.required],
        // [(this.lang === 'ar') ? 'en' : 'ar']: ['']

      }),
      shortDesc: this.fb.group({
        [this.lang]: [this.product.shortDesc, Validators.required],
        // [(this.lang === 'ar') ? 'en' : 'ar']: ['']
      }),
    });
  }

  createNullForm(): void {
    this.productForm = this.fb.group({
      onHome: [''],
      isActive: [''],
      smallParagraphs: [[1]],
      title: this.fb.group({
        ar: ['', Validators.required],
        en: ['', Validators.required]
      }),
      subTitle: this.fb.group({
        ar: ['', Validators.required],
        en: ['', Validators.required]
      }),
      shortDesc: this.fb.group({
        ar: ['', Validators.required],
        en: ['', Validators.required]
      })
    });
  }

  localizedObj(en, ar): FormGroup {
    return this.fb.group({
      ar: [ar, Validators.required],
      en: [en, Validators.required]
    });
  }
}
