import {Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges} from '@angular/core';
import {FormBuilder} from '@angular/forms';
import {Apollo} from 'apollo-angular';
import {getParagraph, getUsername} from '../../../auth/authMutation';

@Component({
  selector: 'app-view-paragraph',
  templateUrl: './view-paragraph.component.html',
  styleUrls: ['./view-paragraph.component.scss']
})
export class ViewParagraphComponent implements OnInit {
  @Output() ids: EventEmitter<any> = new EventEmitter<any>();
  @Output() reloaded: EventEmitter<any> = new EventEmitter<any>();
  @Input() reload: boolean;
  @Input() paragraphs: any;
  selectedIds: any[] = [];
  prevSelectedIds: any[] = [];

  constructor(private fb: FormBuilder, private apollo: Apollo) {

  }

  logo(p): void {
    this.selectedIds.push(p.id);
  }

  done(): void {
    this.ids.emit(this.selectedIds);
  }

  ngOnInit(): void {
  }

}
