import {Component, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {OwlOptions} from 'ngx-owl-carousel-o';
import {allProductsQuery, oneProdQuery, removeProduct} from '../../home/home/homequeries';
import {Apollo} from 'apollo-angular';
import {AuthService} from '../../../services/auth.service';
import {AnimationOptions} from 'ngx-lottie';
import {AnimationItem} from 'lottie-web';

@Component({
  selector: 'app-product',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.scss']
})
export class ProductComponent implements OnInit {
  id: any;
  admin = false;
  product: any;
  loading = true;

  products: any = [];
  customOptions: OwlOptions = {
    center: false,
    autoWidth: false,
    loop: false,
    mouseDrag: true,
    touchDrag: true,
    pullDrag: true,
    dots: false,
    nav: true,
    navSpeed: 700,
    navText: ['<', '>'],
    responsive: {
      0: {
        items: 1
      },
      400: {
        items: 3
      },
      740: {
        items: 3
      },
      940: {
        items: 3
      },
      1024: {
        items: 3
      }
    },
  };

  options: AnimationOptions = {
    path: '/assets/lightbulb.json',
    loop: true,


  };


  constructor(private route: ActivatedRoute, private router: Router, private apollo: Apollo, private auth: AuthService) {
  }

  animationCreated(animationItem: AnimationItem): void {
    animationItem.playSpeed = 2;

  }

  deleteProduct(id) {
    this.loading = true;
    this.apollo.mutate({
      mutation: removeProduct,
      variables: {id: Number(id)}
    }).subscribe((result: any) => {
      this.loading = false;
      this.router.navigateByUrl('/products/list');
    });
  }

  ngOnInit(): void {
    this.auth.checkAdmin().subscribe(x => this.admin = x);

    this.route.params.subscribe(
      params => {
        this.id = params.id;
        this.loading = true;
        this.apollo.watchQuery({
          query: oneProdQuery,
          fetchPolicy: 'network-only',
          variables: {id: Number(this.id)}
        }).valueChanges.subscribe((result: any) => {
          this.loading = false;
          this.products = result?.data?.findManyProducts.entities;
          this.products = this.products.filter((item) => {
            return (Number(item.id) !== Number(this.id));
          });
          this.product = result?.data?.findOneProduct;
        });
      }
    );

  }
}
