import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {SharedModule} from '../../shared/shared.module';
import {ProductComponent} from './product/product.component';
import {RouterModule} from '@angular/router';
import {ProductsListComponent} from './products-list/products-list.component';
import {AddParagraphComponent} from './add-paragraph/add-paragraph.component';
import {ViewParagraphComponent} from './add-paragraph/view-paragraph/view-paragraph.component';
import {AddEditProductComponent} from './add-edit-product/add-edit-product.component';
import {LottieModule} from 'ngx-lottie';
import {playerFactory} from '../pricing/pricing.module';

const routes = [{
  path: 'viewProduct/:id',
  component: ProductComponent
}, {
  path: 'add-paragraph/:id',
  component: AddParagraphComponent
}, {
  path: 'add-paragraph',
  component: AddParagraphComponent
}, {
  path: 'add-edit-product/:id',
  component: AddEditProductComponent
}, {
  path: 'list',
  component: ProductsListComponent
}, {
  path: '',
  redirectTo: 'list'
}];

@NgModule({
  declarations: [ProductComponent, ProductsListComponent, AddParagraphComponent, ViewParagraphComponent, AddEditProductComponent],
  imports: [
    CommonModule, SharedModule, RouterModule.forChild(routes),
    LottieModule.forRoot({player: playerFactory}),
  ]
})
export class ProductsModule {
}
